.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px rgba(0, 0, 0, .1), inset -1px -1px rgba(0, 0, 0, .1), 0 0 4px #4f2ee8;
}

.slider:before {
  content: " ";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  top: 4px;
  left: 1px;
  transform: translate(-1px, -3px);
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(#4f2ee8, #686868);
  box-shadow: inset 1px 1px rgba(0, 0, 0, .1), inset -1px -1px rgba(0, 0, 0, .1), 0 0 4px #4f2ee8;
}

input:checked + .slider:before {
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  top: 4px;
  left: 22px;
  transform: translate(-1px, -3px);
}

